import React, { Component, useState } from 'react';
import {View, FlatList, StyleSheet } from 'react-native';
import colors from "../styles/colors";
import EducationSection from './educationSection.native';

import tc_bg from '../../assets/tc_bg.png';

function EducationListView(){
    const SECTIONS = [
      {
        id:1,
        title: 'B.S. Computer Science',
        image:null,
        hasImage:false,
        imageHeader:["",""],
        schoolHeader:["Bachelor's of Science in Computer Science","The University of Texas at San Antonio"],
        content: [
          {
            id:11,
            text : "\u2022 Concentration in Software Engineering",
          },
          {
            id: 12,
            text: "\u2022 Concentration in Data Science",
          },
          {
            id:13,
            text:"\u2022 Publication + Oral Presentation at the International Conference on Computer Vision",
          },
          {
            id:14,
            text:"\u2022 Senior courses include: Deep Learning, Artificial Intelligence, Data Science, Software Engineering",
          },
        ],
        isVisible:true,
      },
      {
        id:2,
        title: 'M.S. Computer Science',
        image:tc_bg,
        hasImage:true,
        imageHeader:["Machine Learning Engineer II","Toyota Connected North America - Labs"],
        schoolHeader:["Master's of Science in Computer Science","The University of Texas at San Antonio"],
        content: [
          {
            id:23,
            text:"\u2022 Primarily focused on reserach within Artificial Intelligence focused on Computer Vision."
          },
          {
            id:21,
            text:"\u2022 Thesis work: Efficient and Lightweight Encoder-Decoder Architectures for Semantic Segmentation "
          },
          {
            id:22,
            text:"\u2022 Publication + Oral Presentation at the European Conference on Computer Vision"
          },
          
        ],
        isVisible: true,
      },
    ];
  
    return(
      <View style={styles.listContainer}>
        <FlatList
          data={SECTIONS}
          keyExtractor={(item) => item.id.toString()}
          renderItem={({item}) => (
              <EducationSection
                title={item.title}
                content={item.content}
                isVisible={item.isVisible}
                hasImage={item.hasImage}
                image={item.image} 
                imageHeader={item.imageHeader}
                schoolHeader={item.schoolHeader}
              />
          )}
        />
      </View>
    );
  
  }
  
  const styles = StyleSheet.create({
    listContainer:{
      flex:1,
  
    },
  
  });
  
  export default EducationListView;